@import "/src/scss/variables";
/* kb/project-block â€” Notebook grid + two-column layout */

.wp-block-kb-project-block {
	--paper: #fbf6ef;
	--grid: rgba(162, 206, 230, 0.35);
	--ink: #200808;
	--card: #e9e4df;
	--accent: #F47258;

	/* grid paper background */
	background-color: var(--paper);
	background-image:
		linear-gradient(var(--grid) 1px, transparent 1px),
		linear-gradient(90deg, var(--grid) 1px, transparent 1px);
	background-size: 48px 48px;

	padding: clamp(24px, 4vw, 56px);

	/* layout */
	display: grid;
	grid-template-columns: minmax(280px, 520px) 1fr;
	grid-template-areas:
		"title title"
		"left right";
	row-gap: 16px;
	column-gap: clamp(40px, 6vw, 120px);
	align-items: start;
}

/* Title */
.wp-block-kb-project-block .project {
	grid-area: title;
	margin: 0 0 0px;
	color: var(--ink);
	font-size: clamp(36px, 4.4vw, 64px);
	line-height: 1.05;
	letter-spacing: -0.02em;

	font-family: 'Super Chill';
	font-weight: normal;
	font-style: normal;
}


/* Left side (image and button) */
.wp-block-kb-project-block .leftside {
	grid-area: left;
	display: flex;
	flex-direction: column;
	gap: 22px;
}

.wp-block-kb-project-block .photo {
	background: none;
	padding: 0;
	border-radius: 0;
	box-shadow: none;
}

.wp-block-kb-project-block .photo img {
	width: 100%;
	max-width: 520px;
	aspect-ratio: 3 / 4;
	object-fit: cover;
	object-position: top;
	display: block;
	border-radius: 28px;
}

/* Link button   */
.wp-block-kb-project-block a.button.wp-element-button,
.wp-block-kb-project-block .button.wp-element-button {
	display: inline-flex;
	align-items: center;
	justify-content: center;

	width: min(360px, 90%);
	padding: 16px 26px;
	border-radius: 9999px;

	background: var(--accent);
	color: #24100f;
	text-decoration: none;

	font-family: system-ui, -apple-system, Segoe UI, Roboto, sans-serif;
	font-weight: 700;
	font-size: 18px;

	box-shadow: 0 10px 24px rgba(242, 111, 91, 0.25);
}

.wp-block-kb-project-block a.button.wp-element-button:hover {
	filter: brightness(0.98);
	transform: translateY(-1px);
}

/* Right side text */
.wp-block-kb-project-block .rightside {
	grid-area: right;
	padding-top: 10px;
	max-width: 560px;
}

.wp-block-kb-project-block .rightside h2 {
	margin: 0 0 10px;
	color: var(--ink);
	font-size: clamp(28px, 3vw, 44px);
	letter-spacing: 0.08em;
	text-transform: uppercase;

	font-family: "Casual Human", "Bradley Hand", cursive;
	font-weight: 700;
}

/* Overview body */
.wp-block-kb-project-block .overview {
	margin: 0;
	color: var(--ink);
	font-size: 15px;
	line-height: 1.55;
	font-family: "Poppins", Roboto, sans-serif;
	max-width: 520px;
}

/* Mobile stack */
@media (max-width: 900px) {
	.wp-block-kb-project-block {
		grid-template-columns: 1fr;
		grid-template-areas:
			"title"
			"left"
			"right";
	}

	.wp-block-kb-project-block .rightside {
		max-width: 100%;
	}
}
